import { createSlice } from '@reduxjs/toolkit';
const initialState = [];
const chatSlice = createSlice({
    name: 'chat',
    initialState,
    reducers: {
        pushChat: ((state, action) => {
            state.unshift(action.payload);
        })
    }
});
export const { pushChat } = chatSlice.actions;
export default chatSlice.reducer;
